import { ref } from 'vue';

export default function useControllableNumber(amount: number, cycle = true) {
  const selected = ref(0);

  function prev() {
    selected.value = cycle
      ? (selected.value + amount) % (amount + 1)
      : Math.max(selected.value - 1, 0);
  }

  function next() {
    selected.value = cycle
      ? (selected.value + 1) % (amount + 1)
      : Math.min(selected.value + 1, amount);
  }

  return {
    selected,
    prev,
    next,
  };
}
